.bigContainer {
    height: 100vh;
    background-color: #0D0106;
    display: flex;
    flex-direction: column;
    align-items: center;
    position: relative;
}

.container {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    height: 70vh;
    width: 100%;
}

.container img {
    width: 30%;
}

.button {
    width: 28vh;
    height: 8vh;
    background-color: #E5446D;
    color: antiquewhite;
    border-radius: 24px;
    display: flex;
    justify-content: center;
    align-items: center;
    transition: all 0.8s;
}

.buttonContainer {
    position: absolute;
    bottom: 26%;
}

.button h2 {
    margin-bottom: 10px;
    font-size: 32px;
}

.button:hover {
    background-color: #96031A;
    color: #0D0106;
    transform: scale(1.2);
}